<template>
  <div class="g-recharge">
    <div class="m-box" v-for="(v,i) in list" :key="i" @click="diaBit(v.money)">
      <div class="s-img">
        <img src="../assets/images/vip.png" alt="">
      </div>
      <div class="s-level">普通会员</div>
      <div class="s-money">￥{{v.money}}</div>
      <div class="s-distribution">赠{{v.money}}积分</div>
    </div>
    <div class="m-box" @click="rebit">
      <div class="s-img">
        <img src="../assets/images/vip.png" alt="">
      </div>
      <div class="s-level">手动积分充值</div>
      <div class="s-money">10积分起充</div>
      <div class="s-distribution"></div>
    </div>
    <div class="m-dialog" v-show="dialogShow">
      <div>
        <i class="close" @click="diaBit"></i>
        <div class="s-title">选择支付方式</div>
        <div class="s-way">
          <div class="" @click="bit.payment=2">
            <img src="../assets/images/wx.png" alt="">
            <span>微信支付</span>
            <i :class="bit.payment==2&&'active'"></i>
          </div>
          <div class="" @click="bit.payment=1">
            <img src="../assets/images/zfb.png" alt="">
            <span>支付宝支付</span>
            <i :class="bit.payment==1&&'active'"></i>
          </div>
        </div>
        <div class="s-money">支付:￥{{bit.integral}}</div>
        <button @click="submitMsg()">确认</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "recharge",
    data(){
      return {
        list:[],
        dialogShow:false,/*弹框显示*/
        bit:{
          payment:2,
          integral:0
        }
      }
    },
    created(){
      this.recharge();//充值列表
    },
    methods:{
      recharge(){
        this.$post(this.$bee+'/api/index/listRecharge').then((data)=>{
          if(data.code==200){
            data.data.records.forEach((i)=> {
              this.list.push({money: i.money, id: i.id});
            })
          }else{
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
      rebit () {/*手动充值*/
        this.dialog = this.$createDialog({
          type: 'prompt',
          title: '手动充值积分',
          prompt: {
            value: '',
            placeholder: '输入您要充值的数额'
          },
          onConfirm: (e, promptValue) => {
            if(promptValue>0){
              this.bit.integral = promptValue
              this.dialogShow=!this.dialogShow
            }else{
              this.$createToast({
                txt: '请输入正确金额',
                type: 'txt'
              }).show();
            }
          }
        }).show()
      },
      diaBit (v){/*弹框详情*/
        this.dialogShow=!this.dialogShow
        if(v){this.bit.integral = v}
      },
      submitMsg(){
        this.$post(this.$bee+'/api/user/memberRecharge',this.bit).then((data)=>{
          if(data.code==200){
            this.dialogShow=false;
            this.$createToast({
              txt: data.message,
              time: 2000,
              type: 'correct'
            }).show()
          }else{
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      }
    }
  }
</script>

<style scoped lang="stylus">
  .g-recharge
    padding 15px 8px 0
    display flex
    flex-wrap wrap
    .m-box
      width 170px
      height 230px
      box-shadow 0px 5px 13.5px 1.5px rgba(84, 84, 85, 0.3)
      padding 15px 0 10px
      box-sizing border-box
      margin 0 4.5px
      margin-bottom 10px
      .s-img
        img
          width 140px
          height 140px
      .s-level
        color #5c5b5b
        font-size 13.5px
        margin-top 5px
      .s-money
        color: #c38b30;
        font-size 20px
        margin-top 7.5px
      .s-distribution
        font-size 12px
        color: #c38b30;
        margin-top 7.5px
    .m-dialog
      position fixed
      right 0
      left 0
      bottom 0
      top 0
      background rgba(255,255,255,.5)
      &>div
        width 300px
        background #fff
        position absolute
        left calc(50% - 150px)
        top calc(50% - 82px)
        box-shadow: 0px 5px 13.5px 1.5px rgba(84, 84, 85, 0.3)
        .s-title
          font-size 15px
          color #717171
          height 30px
          line-height 30px
          width 100%
          text-align left
          padding-left 20px
        .s-way
          padding-left 15px
          div
            border-top 1px solid #e4e4e4
            height 40px
            line-height 40px
            font-size 0
            text-align left
            padding-left 15px
            position relative
            img
              vertical-align middle
              width 27.5px
            i
              vertical-align middle
              display inline-block
              width 9px
              height 9px
              background url(../assets/images/pay.png) no-repeat center center
              background-size 100% 100%
              position absolute
              right 27px
              top calc(50% - 4.5px)
              &.active
                background-image url(../assets/images/payed.png)
            span
              vertical-align middle
              color #717171
              font-size 12px
              margin-left 10px
        .s-money
          font-size 12px
          color #717171
          margin-top 15px
        &>>>button
          border-radius 6px
          width 140px
          height 26px
          background-color #db8e15
          border 0
          color #fdfdfd
          font-size 14.5px
          margin-top 15px
          margin-bottom 13px
        i.close
          position: absolute
          top 6px
          right 7px
          background url(../assets/images/close.png) no-repeat center center
          background-size 100% 100%
          width 14px
          height 17px
</style>
